<!-- TreeRender -->
<template>
  <span class="rightContent">
    <template>
      <span>
        <Icon v-if="treeData.children==undefined || treeData.children.length==0" type="md-document" style="font-size: 14px; margin-right: 2px"/>
        <Icon v-else type="ios-folder" style="font-size: 15px; margin-right: 2px"/>
        {{treeData.title}}
      </span>
      <span class="btns">
        <Button v-if="treeData.type === 1" type="text" size="small"  icon="md-add" @click="addModal=true, addTreeData = {}, $refs.addTreeData.resetFields()"></Button>
        <Button type="text" size="small" icon="md-create" @click="nodeEdit(treeRoot,treeNode,treeData)"></Button>
        <Button v-if="treeData.fid !== 0" type="text" size="small" icon="md-trash" @click="nodeDel(treeRoot,treeNode,treeData)"></Button>
      </span>
    </template>
    <Modal v-model="addModal">
      <Form ref="addTreeData" :model="addTreeData" :rules="addTreeDataValidate" :label-width="145" style="width: 80%;margin: 40px auto 0">
        <FormItem prop="title" label="图层类型名称：">
          <Input v-model="addTreeData.title" autofocus size="default"/>
        </FormItem>
        <FormItem prop="type" label="是否可以创建子节点：">
          <RadioGroup v-model="addTreeData.type">
            <Radio label="1">是</Radio>
            <Radio label="2">否</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <p slot="footer">
        <Button type="primary" @click="nodeAdd(treeData, addTreeData.title, addTreeData.type)">保存</Button>
        <Button @click="addModal=false">取消</Button>
      </p>
    </Modal>
    <Modal v-model="editModal">
      <Form ref="editTreeData" :model="treeData" :rules="editTreeDataValidate" :label-width="145" style="width: 80%;margin: 40px auto 0">
        <FormItem prop="title" label="图层类型名称：">
          <Input v-model="treeData.title" autofocus size="default"/>
        </FormItem>
        <FormItem prop="type" label="是否可以创建子节点：">
          <RadioGroup v-model="treeData.type">
            <Radio label="1">是</Radio>
            <Radio label="2">否</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <p slot="footer">
        <Button type="primary" @click="nodeEditPass(treeRoot,treeNode,treeData)">保存</Button>
        <Button @click="editModal=false">取消</Button>
      </p>
    </Modal>
  </span>
</template>

<script>
export default {
  name: 'TreeRender',
  components: {},
  data () {
    return {
      // 是否显示修改弹窗
      editModal: false,
      addModal: false,
      addTreeData: {
        title: '',
        type: ''
      },
      /* 添加数据表单校验 */
      addTreeDataValidate: {
        title: [
          { required: true, message: '图层类型名称必须填写', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '是否可以创建子节点必须选择', trigger: 'change' }
        ]
      },
      /* 修改数据表单校验 */
      editTreeDataValidate: {
        title: [
          { required: true, message: '图层类型名称必须填写', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '是否可以创建子节点必须选择', trigger: 'change' }
        ]
      }
      // isEdit: false
    }
  },
  props: {
    // 树的根节点
    treeRoot: {
      type: Array
    },
    // 当前节点
    treeNode: {
      type: Object
    },
    // 当前节点的数据
    treeData: {
      type: Object
    }
  },
  computed: {},
  methods: {
    /* 添加 */
    nodeAdd (d, e, t) {
      this.$refs.addTreeData.validate(valid => {
        if (valid) {
          d.addData = e
          d.type = t
          this.$emit('nodeAdd', d, e, t)
          this.addModal = false
        }
      })
    },
    nodeEdit (r, n, d) {
      this.editModal = true
      // d.isEdit = this.isEdit
      d.title = this.treeData.title
      d.type = String(this.treeData.type)
      // this.$set(d, 'isEdit', d.isEdit)
      this.$set(d, 'title', d.title)
      this.$set(d, 'type', d.type)
      this.$emit('nodeEdit', r, n, d)
    },
    nodeEditPass (r, n, d) { // 编辑完成
      this.$refs.editTreeData.validate(valid => {
        if (valid) {
          this.editModal = false
          // d.isEdit = this.isEdit
          d.title = this.treeData.title
          d.type = String(this.treeData.type)
          // this.$set(d, 'isEdit', d.isEdit)
          this.$set(d, 'title', d.title)
          this.$set(d, 'type', d.type)
          this.$emit('nodeAfterEdit', r, n, d)
        }
      })
    },
    nodeDel (r, n, d) {
      this.$emit('nodeDel', r, n, d)
    }
  },
  watch: {},
  created () {},
  mounted () {}
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  .rightContent {
    width: calc(100% - 22px);
    display: inline-block;
    .btns {
      margin-left: 10%;
      text-align: center;
      padding-left: 10px;
      display: none;
    }
    &:hover {
      .btns {
        display: inline-block;
        .ivu-icon {
          font-size: 14px;
        }
      }
    }
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
</style>
