<template>
  <div id="mobileUserManagement">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>底图类别管理</span>
    </div>
    <div class="wrapper">
      <Card>
        <div style="text-align:left">
          <Tree :data="treeData" :render="renderContent">
          </Tree>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import TreeRender from './TreeRender'
import {
  // 图层类型列表
  getLayerList,
  // 新增
  saveLayer,
  // 修改
  updateLayer,
  // 删除
  deleteLayer
} from '@/api/acquisitionTable'
export default {
  components: {},
  data () {
    return {
      treeData: [],
      // 树结构
      treeList: null
    }
  },
  props: {},
  computed: {},
  methods: {
    renderContent (h, { root, node, data }) {
      let that = this
      return h(TreeRender, {
        props: {
          treeRoot: root,
          treeNode: node,
          treeData: data
        },
        on: {
          nodeAdd: (d) => that.handleAdd(d),
          nodeEdit: (r, n, d) => that.handleEdit(r, n, d),
          nodeDel: (r, n, d) => that.handleDelete(r, n, d),
          nodeAfterEdit: (r, n, d) => that.handleAfterEdit(r, n, d)
        }
      })
    },
    /* axios: 获取图层类型菜单 */
    initTreeList () {
      getLayerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.treeList = res.data.data
          this.getTree(this.treeList)
          this.treeData = this.treeList
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 递归解析
    getTree (parents) {
      if (parents === undefined) {
        return
      }
      parents.forEach(item => {
        item.title = item.name
        // item.expand = true
        this.getTree(item.children)
      })
    },
    // 编辑后，input框失去焦点
    handleAfterEdit (r, n, d) {
      // console.log('after')
      // console.log(d)
      d.name = d.title
      // console.log(d.name)
      let column = {}
      column.name = d.name
      column.id = d.id
      column.fid = d.fid
      column.level = d.level
      column.children = []
      column.type = Number(d.type)
      // console.log(column)
      this.UpdateColumn(column)
    },
    // 编辑中，input框得到焦点
    handleEdit (r, n, d) {
      d.name = d.title
    },
    // 新增方法
    handleAdd (d) {
      //  构建一个实体
      let column = {}
      const children = d.children || []
      // 添加数据
      children.push({
        title: d.addData,
        expand: true,
        parent: d.id,
        type: d.type,
        // isEdit: false,
        children: []
      })
      column.name = d.addData
      column.fid = d.id
      column.level = d.level + 1
      column.children = []
      column.type = Number(d.type)
      this.saveColumn(column)
    },
    saveColumn (column) {
      saveLayer({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '添加成功' })
          // 完成增操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 完成增操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 完成增操作后进行一次查询
        this.initTreeList()
      })
    },
    UpdateColumn (column) {
      updateLayer({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '修改成功' })
          // 改操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 完成增操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 完成增操作后进行一次查询
        this.initTreeList()
      })
    },
    handleDelete (r, n, d) {
      const parentKey = r.find(el => el === n).parent
      const parent = r.find(el => el.nodeKey === parentKey).node
      const index = parent.children.indexOf(d)
      parent.children.splice(index, 1)
      let id = d.id
      this.deleteColumn(id)
    },
    // 根据id查询
    deleteColumn (id) {
      this.$Modal.confirm({
        title: '确认',
        content: '请问您确定要删除该底图类别吗？',
        onOk: () => {
          deleteLayer({
            data: {
              id: [id]
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '删除成功' })
              // 改操作后进行一次查询
              this.initTreeList()
            } else {
              this.$Notice.error({ title: res.data.msg })
              // 改操作后进行一次查询
              this.initTreeList()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            // 改操作后进行一次查询
            this.initTreeList()
          })
        },
        onCancel: () => {
        }
      })
    }
  },
  watch: {},
  created () {
    this.initTreeList()
  },
  mounted () {}
}
</script>

<style scoped lang="scss">
  #mobileUserManagement {
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .wrapper {
      height: calc(100% - 65px);
      overflow: hidden;
      width: 100%;
      padding: 30px 0;
      .ivu-card {
        width: 50%;
        margin: auto;
        max-height: calc(100% - 60px);
        overflow: auto;
        .ivu-tree {
          text-align: left;
          /*overflow: auto;*/
          .ivu-tree-arrow {
            float: left !important;
            margin-right: 10px;
          }
          .ivu-tree-children {
            padding: 0 0 0 40px;
          }
        }
      }
    }
  }
</style>
